@import "../../style/colors"

.Contacts
  .Address
    display: flex
    flex-direction: column
    gap: 8px
    padding-bottom: 30px
    font-weight: 300
    line-height: 1

    .AddressItem
      display: flex
      align-items: center
      gap: 12px

      a
        text-decoration: none
        color: $black

  .Messengers
    display: flex
    justify-content: flex-end
    gap: 8px

  &.ContactsLight
    color: $white-f

    .AddressItem
      a
        color: $white-f
